#portfolio {
    background-color: black;
    opacity: 75%;
    top: 10vh;
    position: relative;
    height: 100vh;
    width: 100vw;
    transition: .6s;
}

#portfolio.hidden {
    top:100vh;
    transition: .6s;
    display: inline !important;
}

.porfolio-page {
    height: 100vh;
    width: 100vw;
    position: fixed;
    background-image: url('../assets/4kbackground.webp');
    background-size: cover;
}
